import request from '../utils/request'
import courseDetail from './detail'
import noData from '../components/noData'
export default {
  name: 'course',
  components: { courseDetail, noData },
  data () {
    return {
      all: [],
      detailItem: {}
    }
  },
  mounted () {
    this.getAll()
  },
  methods: {
    deepClone (original) {
      return JSON.parse(JSON.stringify(original))
    },
    getAll () {
      request({
        url: 'http://www.zhiheyikaoqin.cn/stu/findAllCourse?page=1&size=10',
        method: 'GET'
      }).then(res => {
        if (Number(res.data.errno) !== 0) {
          this.all = []
        } else {
          console.log('dd', res)
      // setTimeout(() => {
      //   console.log('2')
          res = [{
            'subId': 1,
            'stuCode': '1',
            'courseId': 1,
            'subStatus': 301,
            'stuCause': null,
            'createTime': '2019-01-03 16:09:16',
            'updateTime': '2019-01-03 16:09:16',
            'teaCourse': {
              'courseId': 1,
              'teaCode': '6ss',
              teaName: '赵峰',
              'courseName': '数学圆的概念数学圆的概念',
              'courseDate': '2018-12-28 16:00:00',
              'courseStartTime': '2018-12-30 14:32:24',
              'courseEndTime': '2018-12-31 14:32:28',
              'courseStatus': 300,
              'courseCause': '',
              'courseLocation': '计算所',
              'courseInteractive': 0,
              'createTime': '2018-12-28 22:38:27',
              'updateTime': '2018-12-28 22:38:27'
            },
            'feedBack': {
              'feedbackId': 1,
              'subId': 6,
              'stuScore': 5,
              'stuFeedback': '好的',
              'teaScore': 5,
              'teaFeedback': '作为学生干就完了',
              'createTime': '2018-12-29 02:07:31',
              'updateTime': '2018-12-29 10:49:02'
            }
          }, {
            'subId': 1,
            'stuCode': '1',
            'courseId': 1,
            'subStatus': 400,
            'stuCause': null,
            'createTime': '2019-01-03 16:09:16',
            'updateTime': '2019-01-03 16:09:16',
            'teaCourse': {
              'courseId': 1,
              'teaCode': '6ss',
              teaName: '赵峰',
              'courseName': '数学圆的概念2',
              'courseDate': '2018-12-28 16:00:00',
              'courseStartTime': '2018-12-30 14:32:24',
              'courseEndTime': '2018-12-31 14:32:28',
              'courseStatus': 301,
              'courseCause': '',
              'courseLocation': '计算所',
              'courseInteractive': 0,
              'createTime': '2018-12-28 22:38:27',
              'updateTime': '2018-12-28 22:38:27'
            },
            'feedBack': {
              'feedbackId': 1,
              'subId': 6,
              'stuScore': 5,
              'stuFeedback': '好的',
              'teaScore': 5,
              'teaFeedback': '作为学生干就完了',
              'createTime': '2018-12-29 02:07:31',
              'updateTime': '2018-12-29 10:49:02'
            }
          }, {
            'subId': 1,
            'stuCode': '1',
            'courseId': 1,
            'subStatus': 400,
            'stuCause': null,
            'createTime': '2019-01-03 16:09:16',
            'updateTime': '2019-01-03 16:09:16',
            'teaCourse': {
              'courseId': 1,
              'teaCode': '6ss',
              teaName: '赵峰',
              'courseName': '数学圆的概念3',
              'courseDate': '2018-12-28 16:00:00',
              'courseStartTime': '2018-12-30 14:32:24',
              'courseEndTime': '2018-12-31 14:32:28',
              'courseStatus': 302,
              'courseCause': '',
