            'feedBack': {
              'feedbackId': 1,
              'subId': 6,
              'stuScore': 5,
              'stuFeedback': '好的',
              'teaScore': 5,
              'teaFeedback': '作为学生干就完了',
              'createTime': '2018-12-29 02:07:31',
              'updateTime': '2018-12-29 10:49:02'
            }
          }].map(item => {
            item.teaCourse.time = this.getTime(item.teaCourse.courseStartTime) + ' - ' + this.getTime(item.teaCourse.courseEndTime)
            item.status = this.getStatus(Number(item.teaCourse.courseStatus))
            return item
          })
          this.all = this.all.concat(res, res, res, res)
        }
      // }, 0)
      })
    },
    openDetail (item) {
      this.detailItem = this.deepClone(item)
      this.$nextTick(() => {
        this.$refs.detail.showBox()
      })
    },
    getTime (str) {
      const hour = str.split(' ')[1].split(':')[0]
      const min = str.split(' ')[1].split(':')[1]
      return hour + ':' + min
    },
    getStatus (flag) {
      switch (flag) {
        // case 300:
        //   return '待预约'
        // case 301:
        //   return '已预约'
        case 302:
          return '上课中'
        case 303:
          return '已结束'
        case 304:
          return '已失效'
        default:
          return '可预约'
      }
    }
  }
}



